export class UI {
    constructor(opts = {}) {
        this.layer = null;
        this.layers = [];
        // inDialog = false;
        this._done = false;
        this._promise = null;
        if (!opts.canvas)
            throw new Error('Need a canvas.');
        this.canvas = opts.canvas;
        this.loop = opts.loop || GWU.loop;
    }
    get width() {
        return this.canvas.width;
    }
    get height() {
        return this.canvas.height;
    }
    get styles() {
        return defaultStyle;
    }
    // render() {
    //     this.buffer.render();
    // }
    get baseBuffer() {
        const layer = this.layers[this.layers.length - 2] || null;
        return layer ? layer.buffer : this.canvas.buffer;
    }
    get canvasBuffer() {
        return this.canvas.buffer;
    }
    startNewLayer() {
        const layer = new Layer(this);
        this.layers.push(layer);
        if (!this._promise) {
            this._promise = this.loop.run(this);
        }
        this.layer = layer;
        return layer;
    }
    copyUIBuffer(dest) {
        const base = this.baseBuffer;
        dest.copy(base);
        dest.changed = false; // So you have to draw something to make the canvas render...
    }
    finishLayer(layer) {
        GWU.arrayDelete(this.layers, layer);
        if (this.layer === layer) {
            this.layer = this.layers[this.layers.length - 1] || null;
        }
    }
    stop() {
        this._done = true;
        while (this.layer) {
            this.finishLayer(this.layer);
        }
    }
    // run(): Promise<void> {
    //     // this._done = false;
    //     return this.loop.run(this as unknown as GWU.io.IOMap);
    // }
    // stop() {
    //     this._done = true;
    //     if (this.layer) this.layer.stop();
    //     this.layers.forEach((l) => l.stop());
    //     this.layer = null;
    //     this.layers.length = 0;
    // }
    mousemove(e) {
        if (this.layer)
            this.layer.mousemove(e);
        return this._done;
    }
    click(e) {
        if (this.layer)
            this.layer.click(e);
        return this._done;
    }
    keypress(e) {
        if (this.layer)
            this.layer.keypress(e);
        return this._done;
    }
    dir(e) {
        if (this.layer)
            this.layer.dir(e);
        return this._done;
    }
    tick(e) {
        if (this.layer)
            this.layer.tick(e);
        return this._done;
    }
    draw() {
        if (this.layer)
            this.layer.draw();
    }
}
