import * as Text from './text';
import { installWidget } from './make';
export class Button extends Text.Text {
    constructor(layer, opts) {
        super(layer, (() => {
            opts.text = opts.text || '';
            opts.action = opts.action || opts.id;
            opts.tag = opts.tag || 'button';
            return opts;
        })());
    }
    keypress(ev) {
        if (!ev.key)
            return false;
        if (ev.key === 'Enter') {
            const action = this._attrStr('action');
            if (action && action.length)
                this._bubbleEvent(action, this);
            return true;
        }
        return false;
    }
    click(ev) {
        if (!this.contains(ev))
            return false;
        const action = this._attrStr('action');
        if (action && action.length)
            this._bubbleEvent(action, this);
        return true;
    }
}
installWidget('button', (l, opts) => new Button(l, opts));
