import * as GWU from 'gw-utils';
import * as Style from '../style';
Style.defaultStyle.add('*', {
    fg: 'white',
    bg: -1,
    align: 'left',
    valign: 'top',
});
export class Widget {
    constructor(term, opts = {}) {
        this.tag = 'text';
        this.bounds = new GWU.xy.Bounds(0, 0, 0, 1);
        this._depth = 0;
        this.events = {};
        // action: string = '';
        this.children = [];
        this._style = new Style.Style();
        this._parent = null;
        this.classes = [];
        this._props = {};
        this._attrs = {};
        this.layer = term;
        // this.bounds.x = term.x;
        // this.bounds.y = term.y;
        this.bounds.x = opts.x || 0;
        this.bounds.y = opts.y || 0;
        this.bounds.width = opts.width || 0;
        this.bounds.height = opts.height || 1;
        if (opts.tag) {
            this.tag = opts.tag;
        }
        if (opts.id) {
            this.attr('id', opts.id);
            this.attr('action', opts.id);
        }
        if (opts.depth !== undefined) {
            this._depth = opts.depth;
        }
        this._style.set(opts);
        if (opts.class) {
            if (typeof opts.class === 'string') {
                opts.class = opts.class.split(/ +/g);
            }
            this.classes = opts.class.map((c) => c.trim());
        }
        if (opts.tabStop) {
            this.prop('tabStop', true);
        }
        if (opts.action) {
        }
        if (opts.disabled) {
            this.prop('disabled', true);
        }
        if (opts.hidden) {
            this.prop('hidden', true);
        }
        if (opts.action) {
            this.attr('action', opts.action);
        }
        if (this.attr('action')) {
            this.on('click', (_n, w, e) => {
                const action = this._attrStr('action');
                if (action) {
                    this._bubbleEvent(action, w, e);
                }
                return false; // keep bubbling
            });
        }
        this.layer.attach(this);
        this.updateStyle();
    }
    get depth() {
        return this._depth;
    }
    set depth(v) {
        this._depth = v;
        this.layer.sortWidgets();
    }
    get parent() {
        return this._parent;
    }
    set parent(v) {
        this.setParent(v);
    }
    setParent(v, opts = {}) {
        if (this._parent) {
            this._parent._removeChild(this);
        }
        this._parent = v;
        if (this._parent) {
            this.depth = this._depth || this._parent.depth + 1;
            this._parent._addChild(this, opts);
        }
    }
    pos(x, y) {
        if (x === undefined)
            return this.bounds;
        if (typeof x === 'number') {
            this.bounds.x = x;
            this.bounds.y = y || 0;
        }
        else {
            this.bounds.x = x.x;
            this.bounds.y = x.y;
        }
        this.layer.needsDraw = true;
        return this;
    }
    text(v) {
        if (v === undefined)
            return this._attrStr('text');
        this.attr('text', v);
        return this;
    }
    attr(name, v) {
        if (v === undefined)
            return this._attrs[name];
        this._attrs[name] = v;
        return this;
    }
    _attrInt(name) {
        const n = this._attrs[name] || 0;
        if (typeof n === 'number')
            return n;
        if (typeof n === 'string')
            return Number.parseInt(n);
        return n ? 1 : 0;
    }
    _attrStr(name) {
        const n = this._attrs[name] || '';
        if (typeof n === 'string')
            return n;
        if (typeof n === 'number')
            return '' + n;
        return n ? 'true' : 'false';
    }
    _attrBool(name) {
        return !!this._attrs[name];
    }
    prop(name, v) {
        if (v === undefined)
            return this._props[name];
        const current = this._props[name];
        if (current !== v) {
            this._setProp(name, v);
        }
        return this;
    }
    _setProp(name, v) {
        this._props[name] = v;
        // console.log(`${this.tag}.${name}=${v}`);
        this.updateStyle();
    }
    _propInt(name) {
        const n = this._props[name] || 0;
        if (typeof n === 'number')
            return n;
        if (typeof n === 'string')
            return Number.parseInt(n);
        return n ? 1 : 0;
    }
    _propStr(name) {
        const n = this._props[name] || '';
        if (typeof n === 'string')
            return n;
        if (typeof n === 'number')
            return '' + n;
        return n ? 'true' : 'false';
    }
    _propBool(name) {
        return !!this._props[name];
    }
    toggleProp(name) {
        const current = !!this._props[name];
        this.prop(name, !current);
        return this;
    }
    incProp(name, n = 1) {
        let current = this.prop(name) || 0;
        if (typeof current === 'boolean') {
            current = current ? 1 : 0;
        }
        else if (typeof current === 'string') {
            current = Number.parseInt(current) || 0;
        }
        current += n;
        this.prop(name, current);
        return this;
    }
    contains(...args) {
        return this.bounds.contains(args[0], args[1]);
    }
    style(opts) {
        if (opts === undefined)
            return this._style;
        this._style.set(opts);
        this.updateStyle();
        return this;
    }
    addClass(c) {
        const all = c.split(/ +/g);
        all.forEach((a) => {
            if (this.classes.includes(a))
                return;
            this.classes.push(a);
        });
        return this;
    }
    removeClass(c) {
        const all = c.split(/ +/g);
        all.forEach((a) => {
            GWU.arrayDelete(this.classes, a);
        });
        return this;
    }
    hasClass(c) {
        const all = c.split(/ +/g);
        return GWU.arrayIncludesAll(this.classes, all);
    }
    toggleClass(c) {
        const all = c.split(/ +/g);
        all.forEach((a) => {
            if (this.classes.includes(a)) {
                GWU.arrayDelete(this.classes, a);
            }
            else {
                this.classes.push(a);
            }
        });
        return this;
    }
    get focused() {
        return !!this.prop('focus');
    }
    // @return true to stop the focus change event
    focus(reverse = false) {
        if (this.prop('focus'))
            return true;
        this.prop('focus', true);
        return this._fireEvent('focus', this, { reverse });
    }
    // @return true to stop the focus change event
    blur(reverse = false) {
        if (!this.prop('focus'))
            return false;
        this.prop('focus', false);
        return this._fireEvent('blur', this, { reverse });
    }
    get hovered() {
        return !!this.prop('hover');
    }
    set hovered(v) {
        this.prop('hover', v);
    }
    get hidden() {
        let current = this;
        while (current) {
            if (current.prop('hidden'))
                return true;
            current = current.parent;
        }
        return false;
    }
    set hidden(v) {
        this.prop('hidden', v);
    }
    updateStyle() {
        this._used = this.layer.styles.computeFor(this);
        this.layer.needsDraw = true; // changed style or state
    }
    draw(buffer) {
        if (this.hidden)
            return false;
        return this._draw(buffer);
    }
    _draw(buffer) {
        this._drawFill(buffer);
        return true;
    }
    _drawFill(buffer) {
        buffer.fillRect(this.bounds.x, this.bounds.y, this.bounds.width, this.bounds.height, ' ', this._used.bg, this._used.bg);
    }
    childAt(...args) {
        return this.children.find((c) => c.contains(args[0], args[1])) || null;
    }
    _addChild(w, opts) {
        let beforeIndex = -1;
        if (opts && opts.beforeIndex !== undefined) {
            beforeIndex = opts.beforeIndex;
        }
        if (w._parent && w._parent !== this)
            throw new Error('Trying to add child that already has a parent.');
        if (!this.children.includes(w)) {
            if (beforeIndex < 0 || beforeIndex >= this.children.length) {
                this.children.push(w);
            }
            else {
                this.children.splice(beforeIndex, 0, w);
            }
        }
        w._parent = this;
        return this;
    }
    _removeChild(w) {
        if (!w._parent || w._parent !== this)
            throw new Error('Removing child that does not have this widget as parent.');
        w._parent = null;
        GWU.arrayDelete(this.children, w);
        return this;
    }
    resize(w, h) {
        this.bounds.width = w || this.bounds.width;
        this.bounds.height = h || this.bounds.height;
        this.layer.needsDraw = true;
        return this;
    }
    // Events
    mouseenter(e, over) {
        if (!this.contains(e))
            return;
        if (this.hovered)
            return;
        this.hovered = true;
        this._fireEvent('mouseenter', this, e);
        if (this._parent) {
            this._parent.mouseenter(e, over);
        }
    }
    mousemove(e) {
        if (this.contains(e) && !e.defaultPrevented && !this.hidden) {
            this._fireEvent('mousemove', this, e);
            // e.preventDefault();
        }
        else {
            this.mouseleave(e);
        }
        return false;
    }
    mouseleave(e) {
        if (this.contains(e))
            return;
        if (!this.hovered)
            return;
        this.hovered = false;
        this._fireEvent('mouseleave', this, e);
        // if (this._parent) {
        //     this._parent.mouseleave(e);
        // }
    }
    click(e) {
        if (this.hidden)
            return false;
        return this._fireEvent('click', this, e);
    }
    keypress(e) {
        return this._fireEvent('keypress', this, e);
    }
    dir(e) {
        return this._fireEvent('dir', this, e);
    }
    tick(e) {
        return this._fireEvent('tick', this, e);
    }
    on(event, cb) {
        let handlers = this.events[event];
        if (!handlers) {
            handlers = this.events[event] = [];
        }
        if (!handlers.includes(cb)) {
            handlers.push(cb);
        }
        return this;
    }
    off(event, cb) {
        let handlers = this.events[event];
        if (!handlers)
            return this;
        if (cb) {
            GWU.arrayDelete(handlers, cb);
        }
        else {
            handlers.length = 0; // clear all handlers
        }
        return this;
    }
    _fireEvent(name, source, args) {
        const handlers = this.events[name] || [];
        let handled = handlers.reduce((out, h) => h(name, source || this, args) || out, false);
        return handled;
    }
    _bubbleEvent(name, source, args) {
        let current = this;
        while (current) {
            if (current._fireEvent(name, source, args))
                return true;
            current = current.parent;
        }
        return false;
    }
}
