// INQUIRY
export class Inquiry {
    constructor(widget) {
        this._prompts = [];
        this._result = {};
        this._stack = [];
        this._current = null;
        this.widget = widget;
        this._keypress = this._keypress.bind(this);
        this._change = this._change.bind(this);
    }
    prompts(v, ...args) {
        if (Array.isArray(v)) {
            this._prompts = v.slice();
        }
        else {
            args.unshift(v);
            this._prompts = args;
        }
        return this;
    }
    _finish() {
        this.widget.off('keypress', this._keypress);
        this.widget.off('change', this._change);
        this._resolve(this._result);
    }
    _cancel() {
        this.widget.off('keypress', this._keypress);
        this.widget.off('change', this._change);
        this._reject('Quit');
    }
    start() {
        this._current = this._prompts[0];
        this._result = {};
        this.widget.on('keypress', this._keypress);
        this.widget.on('change', this._change);
        this.widget.showPrompt(this._current, this._result);
        return new Promise((resolve, reject) => {
            this._resolve = resolve;
            this._reject = reject;
        });
    }
    back() {
        this._current.reset();
        this._current = this._stack.pop() || null;
        if (!this._current) {
            this._cancel();
        }
        else {
            this._current.reset(); // also reset the one we are going back to
            this._result = {};
            this._prompts.forEach((p) => p.updateResult(this._result));
            this.widget.showPrompt(this._current, this._result);
        }
    }
    restart() {
        this._prompts.forEach((p) => p.reset());
        this._result = {};
        this._current = this._prompts[0];
        this.widget.showPrompt(this._current, this._result);
    }
    quit() {
        this._cancel();
    }
    _keypress(_n, _w, e) {
        if (!e.key)
            return false;
        if (e.key === 'Escape') {
            this.back();
            return true;
        }
        else if (e.key === 'R') {
            this.restart();
            return true;
        }
        else if (e.key === 'Q') {
            this.quit();
            return true;
        }
        return false;
    }
    _change(_n, _w, p) {
        p.updateResult(this._result);
        const next = p.next();
        if (next) {
            this._current = this._prompts.find((p) => p.id() === next) || null;
            if (this._current) {
                this._stack.push(p);
                this.widget.showPrompt(this._current, this._result);
                return true;
            }
        }
        this._finish();
        return true;
    }
}
